import React, { useState } from "react";
import axios from "axios";
import "../style/attribute-table.css";

type Props = {
  setKey: (key: string) => void;
};

export const SignInForm = ({ setKey }: Props) => {
  const [userName, setUserName] = useState<string | undefined>("");
  const [password, setPassword] = useState<string | undefined>("");
  const [requestInProgress, setRequestInProgress] = useState<boolean>(false);

  const getKey = () => {
    setRequestInProgress(true);
    let identity = "userId=" + userName + "&password=" + password;

    axios
      .post("https://astro-sandbox.stibo.com/graphqlv2/auth?" + identity, {})
      .then((response) => {
        setRequestInProgress(false);
        setKey(response.data);
      })
      .catch(() => {
        setRequestInProgress(false);
        alert("An error occurred in the request");
      });
  };
  const handleFormSubmit = (event: React.FormEvent) => {
    event.preventDefault();
    getKey();
  };

  return (
    <main className="sign-in-main">
      <div className="SignIn">
        <form onSubmit={handleFormSubmit}>
          <div>
            <label>Username:</label>
            <input
              id="input"
              defaultValue={userName}
              onChange={(event) => setUserName(event.target.value)}
              disabled={requestInProgress}
            />
          </div>
          <div>
            <label>Password:</label>
            <input
              type="password"
              defaultValue={password}
              onChange={(event) => setPassword(event.target.value)}
              disabled={requestInProgress}
            />
          </div>
          <div>
            <button type="submit" onClick={getKey} disabled={requestInProgress}>
              Login
            </button>
            <button
              type="button"
              onClick={() => setKey("nokey")}
              disabled={requestInProgress}
            >
              Continue without login
            </button>
          </div>
        </form>
      </div>
    </main>
  );
};
