import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Astrocytia from "../../assets/images/astrocytia.png";
import Dims from "../../assets/icons/dims.png";
import { Search } from "./Search";
import { TopBar } from "./TopBar";
import "../../style/header.css";

export const Header = () => {
  const [pageTitle, setPageTitle] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    setPageTitle(document.title);
    console.log(document.title);
  }, []);

  const handleClick = () => {
    navigate(
      "/?context=Context1&workspace=Main&nodeId=WebFashion%26Lifestyle&nodeType=Classification"
    );
  };

  return (
    <div>
      <TopBar />
      <header>
        <div className="header-div">
          <img
            className="logo"
            src={Astrocytia}
            alt="logo"
            onClick={handleClick}
          />
          <img className="dims" src={Dims} alt="icon" />
        </div>
        <Search />
      </header>
    </div>
  );
};
