import React from "react";
import { Node } from "../types/nodeTypes";

type DisplayAttributeProps = {
  node: Node;
  attributeId: string;
};

export const DisplayAttribute: React.FC<DisplayAttributeProps> = ({
  node,
  attributeId,
}) => {
  const attributeValue = node.values?.find(
    (value) => attributeId === value.attribute.id
  )?.simpleValue || "";

  return <div>{attributeValue}</div>;
};
