import React, { useState, useEffect, useRef } from "react";
import "font-awesome/css/font-awesome.min.css";
import "../../style/header.css";
import User from "../../assets/icons/user-white.png";
import { invalidateAuthenticationToken } from "../../auth/Auth";
import {SelectedCounter} from "../SelectedCounter";
import { Product } from "../../types/nodeTypes";

 
type Props = {
  products: Product[];
};

export const TopBar: React.FC = () => {
  const [isActive, setIsActive] = useState(false);
  const topBarRef = useRef<HTMLDivElement>(null);

  const menuToggle = () => {
    setIsActive(!isActive);
  };

  const handleLogout = () => {
    invalidateAuthenticationToken();
  };

  const handleClickOutside = (event: MouseEvent) => {
    if (
      topBarRef.current &&
      !topBarRef.current.contains(event.target as Node)
    ) {
      setIsActive(false);
    }
  };

  useEffect(() => {
    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  return (
    <div className="topbar-wrap" ref={topBarRef}>
      
      <SelectedCounter />



      <div className="topbar">
        <div className="profile" onClick={menuToggle}>
          <img className="user-icon" src={User} alt="profile" />
        </div>
        <div className={`topbar-menu ${isActive ? "active" : ""}`}>
          <h3>
            Sommer
            <p>soch@astrocytia.com</p>
          </h3>
          <ul>
            <li className="topbar-list">
              <i className="fa fa-user"></i>
              <a href="#">My Profile</a>
            </li>
            <li className="topbar-list">
              <i className="fa fa-envelope"></i>
              <a href="#">Inbox</a>
            </li>
            <li className="topbar-list">
              <i className="fa fa-cog"></i>
              <a href="#">Settings</a>
            </li>
            <li className="topbar-list">
              <i className="fa fa-question"></i>
              <a href="#">Help</a>
            </li>
            <li className="topbar-list" onClick={handleLogout}>
              <i className="fa fa-sign-out"></i>
              <a href="#">Logout</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};
