import React, {useContext} from "react";
import {AppContext, AppContextI} from "../../ContextWrapper";
import Asset from "../../assets/icons/icon.png";
import {ClickableImage} from "../assets/ClickableImage";
import {CardColumn} from "../blocks/CardColumn";

type Props = {
  classification: any;
};

export const ClassificationSummary = ({classification}: Props) => {
  const appContext = useContext<AppContextI>(AppContext);

  const topContent = (
      <ClickableImage
          linkedNode={classification}
          asset={classification.primaryImage}
      />
  );

  return (
    <CardColumn
      topContent={topContent}
      bottomIcon={Asset}
      bottomTitle={classification.title}
      bottomOnClick={() => appContext.setSelection(classification)}
    />
  );
};
