import {Node} from "../../types/nodeTypes";
import {NODE_TYPE_PRODUCT} from "../../utils/constants";
import {ProductSummary} from "./ProductSummary";
import {ClassificationSummary} from "./ClassificationSummary";

type Props = {
    node: Node
}

export const NodeSummary = ({node}: Props) => {
    const typename = node.__typename;
    return NODE_TYPE_PRODUCT === typename ?
        <ProductSummary product={node}/>
        : NODE_TYPE_PRODUCT === typename ?
            <ClassificationSummary classification={node}/>
            : <div>Node type '{typename}' is not supported</div>;
}