import { createContext, useEffect, useState } from "react";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import {
  CONTEXT,
  NODE_ID,
  NODE_TYPE,
  NODE_TYPE_CLASSIFICATION,
  NODE_TYPE_PRODUCT,
  WORKSPACE,
} from "./utils/constants";
import { Node, Product } from "./types/nodeTypes";
import { ApolloError, ServerError } from "@apollo/client";
import { invalidateAuthenticationToken } from "./auth/Auth";
import { useToast } from "@chakra-ui/react";

type Props = {
  children: any;
};

export interface AppContextI {
  getContext: () => string | undefined;
  setContext: (context: string) => void;

  getWorkspace: () => string | undefined;
  setWorkspace: (workspace: string) => void;

  getNodeId: () => string | undefined;

  getNodeType: () => string | undefined;

  isProductSelection: () => Boolean;
  isClassificationSelection: () => Boolean;

  setSelection: (child: Node, replace?: boolean) => void;

  // Always use this for all queries to get error handling (e.g. token expiration) and
  // to automatically obtain context, workspace and nodeId (if set in the URL)
  getRequestVariables: (extraVariables?: any) => any | undefined;
  getChannel: () => string | undefined;
  setChannel: (node: Node) => void;
  showError(message: string): void;


  selectedProducts: Product[];
  setSelectedProducts: React.Dispatch<React.SetStateAction<Product[]>>;
}

export const AppContext = createContext<AppContextI>({
  getContext: () => undefined,
  setContext: () => undefined,

  getWorkspace: () => undefined,
  setWorkspace: () => undefined,

  getNodeId: () => undefined,

  getNodeType: () => undefined,

  isClassificationSelection: () => false,
  isProductSelection: () => false,

  setSelection: () => undefined,

  getRequestVariables: () => undefined,

  getChannel: () => undefined,
  setChannel: () => undefined,
  showError: () => undefined,


  selectedProducts: [],
  setSelectedProducts: () => undefined,
});

export const ContextWrapper = ({ children }: Props) => {
  //invalidateAuthenticationToken();
  const [searchParams, setSearchParams] = useSearchParams();
  const [context, setContext] = useState<string>("English All");
  const [workspace, setWorkspace] = useState<string>("Main");
  const [nodeId, setNodeId] = useState<string | undefined>(undefined);
  const [nodeType, setNodeType] = useState<string | undefined>(undefined);
  const toast = useToast();

  const navigate = useNavigate();
  const location = useLocation();

  const [selectedProducts, setSelectedProducts] = useState<Product[]>([]);


  function getNodeId() {
    return searchParams.get(NODE_ID);
  }

  function getNodeType() {
    return searchParams.get(NODE_TYPE);
  }

  useEffect(() => {
    let contxt = searchParams.get("context");
    if (!contxt) {
      contxt = context;
      searchParams.set("context", context);
    }
    setContext(contxt);

    let workspc = searchParams.get(WORKSPACE);
    if (!workspc) {
      workspc = workspace;
      searchParams.set(WORKSPACE, workspace);
    }
    setWorkspace(workspc);

    const nodeTp = getNodeType();
    setNodeType(nodeTp || undefined);

    const ndId = getNodeId();
    setNodeId(ndId || undefined);

    setSearchParams(searchParams, { replace: true });
  }, [searchParams]);

  const setUrlContext = (cntxt: string) => {
    searchParams.set("context", cntxt);
    setSearchParams(searchParams);
  };

  const setUrlWorkspace = (wrkspc: string) => {
    searchParams.set(WORKSPACE, wrkspc);
    setSearchParams(searchParams);
  };

  const updateUrlFromNode = (node: Node, replace?: boolean) => {
    const nodeId = searchParams.get(NODE_ID);
    const nodeType = searchParams.get(NODE_TYPE);
    if (node.id !== nodeId || node.__typename !== nodeType) {
      searchParams.set(NODE_ID, node.id);
      searchParams.set(NODE_TYPE, node.__typename);
      setSearchParams(searchParams, {
        replace: replace,
      });
    }
  };

  const updateChannelFromNode = (node: Node) => {
    searchParams.set(NODE_ID, node.id);
    searchParams.set(NODE_TYPE, node.__typename);
    searchParams.set(CONTEXT, context);
    searchParams.set(WORKSPACE, workspace);
    const paramString = searchParams.toString();
    navigate("/" + node.id + "?" + paramString);
  };

  const getChannelName = () => {
    const strings = location.pathname.split("/");
    return strings && strings.length > 1 ? strings[1] : undefined;
  };

  return (
    <AppContext.Provider
      value={{
        getContext: () => context,
        selectedProducts,  
        setSelectedProducts,
        setContext: setUrlContext,
        getWorkspace: () => workspace,
        setWorkspace: setUrlWorkspace,
        getNodeId: () => nodeId || getNodeId() || undefined, // ensure that the nodeId is correct even at first load before the useEffect() is triggered
        getNodeType: () => nodeType || getNodeType() || undefined, // ensure that the nodeType is correct even at first load before the useEffect() is triggered
        isProductSelection: () => NODE_TYPE_PRODUCT === nodeType,
        isClassificationSelection: () => NODE_TYPE_CLASSIFICATION === nodeType,
        setSelection: updateUrlFromNode,
        getRequestVariables: (extraVariables: any) => {
          return {
            variables: {
              ...extraVariables,
              context: context,
              workspace: workspace,
              id: nodeId || getNodeId() || undefined,
            },
            onError: (error: ApolloError) => {
              const serverError: ServerError =
                error.networkError as ServerError;
              if (serverError) {
                if (401 === serverError.statusCode) {
                  invalidateAuthenticationToken();
                }
              }
            },
          };
        },
        getChannel: getChannelName,
        setChannel: updateChannelFromNode,
        showError: (message: string) => {
          toast({
            status: "error",
            title: message,
          });
        },
      }}
    >
      {children}
    </AppContext.Provider>
  );
};
