import {ApolloClient, ApolloProvider, HttpLink, InMemoryCache} from "@apollo/client";
import React from "react";
import {SignInForm} from "./SignInForm";
import {localResolvers} from "../gql/localResolvers";
import {ASTRO_SANDBOX_URL} from "../utils/constants";

type Props = {
    children: any;
}

const KEY_STRING = "key";

export const invalidateAuthenticationToken = () => {
    localStorage.removeItem(KEY_STRING);
    window.location.reload();
}

export const Auth = ({children}: Props) => {
    const doSetKey = (key: string) => {
        localStorage.setItem(KEY_STRING, key);
        window.location.reload();
    }

    const httpLink = new HttpLink({
        uri: ASTRO_SANDBOX_URL + '/graphqlv2/graphql',
        headers: {
            authorization: 'Bearer ' + localStorage.getItem(KEY_STRING)
        },
        fetchOptions: {}
    })

    const client = new ApolloClient({
        link: httpLink,
        cache: new InMemoryCache(),
        resolvers: localResolvers
    });

    return <ApolloProvider client={client}>
        {
            localStorage.getItem(KEY_STRING) ?
                <div>
                    {children}
                </div>
                :
                <SignInForm setKey={doSetKey}/>
        }
    </ApolloProvider>
}