import React from "react";
import { ClickableImage } from "../assets/ClickableImage";
import { Product } from "../../types/nodeTypes";
import { DisplayAttribute } from "../DisplayAttribute";

type CardRowProps = {
  product: Product;
  onClick: () => void;
  primaryImage: any;
  isSelected?: boolean;
};
export const CardRow: React.FC<CardRowProps> = ({
  product,
  onClick,
  primaryImage,
  isSelected,
}) => {
  return (
    <div 
      className={`card-row ${isSelected ? 'selected' : ''}`} 
      key={product.id} 
      onClick={onClick}
    >
      <div className="card-row-img">
        <ClickableImage linkedNode={product} asset={primaryImage} />
      </div>
      <div className="card-row-text">
        <h3>{product.title}</h3>
      </div>
    </div>
  );
};

