import React from 'react';

type CollapsibleProps = {
  id: string;
  title: string;
  children: React.ReactNode;
};

export const Collapsible = ({ id, title, children }: CollapsibleProps): JSX.Element => (
  <div>
    <input type="checkbox" id={id} className="toggle" />
    <label htmlFor={id} className="collapsible">
      {title}
    </label>
    <div className="content">
      <p>{children}</p>
    </div>
  </div>
);


