import React, { useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { FaShoppingCart } from "react-icons/fa";
import { Product } from "../types/nodeTypes";
import { AppContext, AppContextI } from "../ContextWrapper";

type Props = {
  products?: Product[];
};

export const SelectedCounter: React.FC<Props> = ({ products }) => {
  const appContext = useContext<AppContextI>(AppContext);
  const { selectedProducts, setSelectedProducts } = appContext;
  const navigate = useNavigate();

  useEffect(() => {
    setSelectedProducts([]);
  }, [products]);

  const navigateToSelectedProducts = () => {
    navigate("/selected");
  };

  return (
    <div className="cart-container">
      <FaShoppingCart
        className="cart-icon"
        onClick={navigateToSelectedProducts}
        title="Click to view all your selected products"

      />
      <p className="cart-count">{selectedProducts.length}</p>
    </div>
  );
};
