import React from "react";

type CardColumnProps = {
  topContent: React.ReactNode;
  bottomIcon?: string;
  bottomTitle?: string;
  bottomOnClick: () => void;
};

export const CardColumn : React.FC<CardColumnProps> = ({ topContent, bottomIcon, bottomTitle, bottomOnClick }) => {
  return (
    <div className="card-column">
      <div className="card-column-top">{topContent}</div>
      <a className="card-column-bottom" onClick={bottomOnClick}>
        {bottomIcon && <div className="card-column-icon"><img src={bottomIcon} alt="asset" /></div>}
        {bottomTitle && <h3 className="card-column-title">{bottomTitle}</h3>}
      </a>
    </div>
  );
};
