import {Node} from "../types/nodeTypes";

export const getAndEncodeAssetURI = (asset?: any) => {
    if (asset) {
        let contentUri = asset.contentUri;
        const assetId = asset.id;
        const replaceValue = assetId?.replace("&", "%26");
        return contentUri?.replace(assetId, replaceValue);
    }
    return undefined;
}

export const resolveReferences = (node?: any): Node[] => {
    let refSources: Node[] = [];
    const refTypes = node?.incomingReferencesByReferenceType;
    refTypes?.map((refType: any) => {
        // We need to dig out the product assets from the references...
        const pageElements = refType.referenceEntries?.pageElements;
        const products = pageElements?.map((elem: any) => {
            const assets = elem.source?.referencesByReferenceType[0]?.referenceEntries.map((entry: any) => {
                return entry.target;
            })
            return {...elem.source, assets: assets};
        });
        refSources = [...refSources, ...products];
    });
    return refSources;
}

export const resolvePrimaryImage = (elem: any) => elem.primaryImages[0]?.referenceEntries[0]?.target;