import {useContext, useEffect} from "react";
import {AppContext, AppContextI} from "../../ContextWrapper";
import {NodeHierarchy} from "./NodeHierarchy";
import {NODE_TYPE_CLASSIFICATION} from "../../utils/constants";

export const WebHierarchy = () => {
    const appContext = useContext<AppContextI>(AppContext);

    const nodeId = appContext.getNodeId();
    const nodeType = appContext.getNodeType();

    const channel = appContext.getChannel();

    useEffect(() => {
        if (!nodeId && !nodeType && channel) {
            appContext.setSelection({
                id: channel,
                type: NODE_TYPE_CLASSIFICATION,
                __typename: NODE_TYPE_CLASSIFICATION,
                title: ""
            });
        }
    }, [nodeId, nodeType, channel]);

    if (nodeId && nodeType) {
        return <NodeHierarchy/>
    }
    return <div>No web classification found</div>
}