import Placeholder from "../../assets/images/placeholder.png";
import React, {useContext} from "react";
import {AppContext, AppContextI} from "../../ContextWrapper";
import {Asset, Node} from "../../types/nodeTypes";
import {getAndEncodeAssetURI} from "../../utils/utils";

type Props = {
    asset?: Asset,
    linkedNode?: Node
}

export const ClickableImage = ({asset, linkedNode}: Props) => {
    const appContext = useContext<AppContextI>(AppContext);
    const contentUri: string = getAndEncodeAssetURI(asset);

    const assetElement = <>{contentUri ?
        <img alt={linkedNode?.name} src={contentUri}/>
        :
        <img src={Placeholder} alt="image"/>
    }</>;

    return linkedNode ?
        <a onClick={() => {
            appContext.setSelection(linkedNode)
        }}>
            {assetElement}
        </a>
        :
        <>
            {assetElement}
        </>
}