import React, { useEffect, useState } from "react";
import "../style/edit.css";
import { Node } from "../types/nodeTypes";
import { EditField } from "./EditField";

type EditFieldProps = {
  node: Node;
  attributeId: string;
  tag: keyof JSX.IntrinsicElements;
  onUpdate: (newValue: string) => void;
};

export const EditAttributeField: React.FC<EditFieldProps> = ({
  node,
  attributeId,
  onUpdate,
  tag,
}) => {
  const [originalValue, setOriginalValue] = useState<string>("");

  useEffect(() => {
    const value = node.values?.find(
      (value) => attributeId === value.attribute.id
    );
    setOriginalValue(value?.simpleValue || "");
  }, [node]);

  return <EditField value={originalValue} tag={tag} onUpdate={onUpdate} />;
};
