import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AppContext } from "../ContextWrapper";
import { CardRow } from "./blocks/CardRow";
import "../style/selected-product-page.css"  

export const SelectedProductPage: React.FC = () => {
  const { selectedProducts } = useContext(AppContext);

  const navigate = useNavigate();

  const goBack = () => {
    navigate(-1); 
  };
  return (
    <main className="product-page-main">
      <h2>Selected Products</h2>
      <div className="go-back" onClick={goBack}>Go Back</div>

      {selectedProducts.length === 0 ? (
        <p>No products selected.</p>
      ) : (
        <div className="selected-product-container">
          {selectedProducts.map((product) => (
            <CardRow 
              product={product} 
              onClick={() => {}} 
              primaryImage={product.primaryImage} 
              isSelected={true} 
              key={product.id}
            />
          ))}
        </div>
      )}
    </main>
  );
};
