import React, { useContext, useEffect, useState } from "react";
import { AppContext, AppContextI } from "../../ContextWrapper";
import { Classification, Node, Product } from "../../types/nodeTypes";
import { useLazyQuery } from "@apollo/client";
import { GET_CLASSIFICATION_RELATIONS } from "../../gql/classificationQueries";
import { NodeSummary } from "../details/NodeSummary";
import "../../style/categories.css";
import { ClassificationSummary } from "../details/ClassificationSummary";
import { resolvePrimaryImage } from "../../utils/utils";
import { FilterList } from "../blocks/FilterList";

type Props = {
  // Optional list of nodes. Only used if no node is selected
  childNodes?: Classification[];
};

function resolveReferences(data: any) {
  return data.classification?.children.pageElements;
}

function resolveAssets(pageElements: any[], refSources: any[]) {
  const sources = pageElements?.map((elem: any) => {
    const primaryImage = resolvePrimaryImage(elem);

    return {
      ...elem,
      primaryImages: undefined,
      primaryImage: primaryImage,
    };
  });
  return [...refSources, ...sources];
}

export const ClassificationHierarchy = ({ childNodes }: Props) => {
  const appContext = useContext<AppContextI>(AppContext);
  const { selectedProducts, setSelectedProducts } = appContext;
  const [referencedSources, setReferencedSources] = useState<Node[]>([]);
  const [selectMode, setSelectMode] = useState(false);

  useEffect(() => {
    document.title = "Classification Hierarchy";
  }, []);

  const [getClassificationData, { data }] = useLazyQuery(
    GET_CLASSIFICATION_RELATIONS,
    appContext.getRequestVariables()
  );

  useEffect(() => {
    if (appContext.isClassificationSelection()) {
      getClassificationData();
    }
  }, [appContext.getNodeId()]);

  let childElements: Node[] | undefined = undefined;

  if (data) {
    childElements = resolveReferences(data);
    childNodes = childElements?.map((elem: any) => {
      const primaryImage = resolvePrimaryImage(elem);
      return {
        ...elem,
        primaryImages: undefined,
        primaryImage: primaryImage,
      } as Classification | Product;
    });
  }

  useEffect(() => {
    if (childElements && childElements.length === 1) {
      appContext.setSelection(childElements[0], true);
    } else if (data) {
      let refSources: any[] = [];
      const refTypes = data?.classification?.incomingReferencesByReferenceType;
      refTypes?.map((refType: any) => {
        // We need to dig out the product assets from the references...
        // TODO: Make general mapping from queries to objects
        const pageElements = refType.referenceEntries?.pageElements.map(
          (elem: any) => elem.source
        );
        refSources = resolveAssets(pageElements, refSources);
      });
      setReferencedSources(refSources);
    }
  }, [data]);

  const toggleSelectMode = () => {
    setSelectMode((prevMode) => !prevMode);
  };

  const toggleSelectedProduct = (product: Product) => {
    setSelectedProducts((prevSelectedProducts) => {
      const isAlreadySelected = prevSelectedProducts.some(
        (p) => p.id === product.id
      );
      return isAlreadySelected
        ? prevSelectedProducts.filter((p) => p.id !== product.id)
        : [...prevSelectedProducts, product];
    });
  };

  return (
    <main className="category-main">
      {childNodes && referencedSources.length === 0 ? (
        <div className="container category-container">
          {childNodes.map((child: any) => (
            <ClassificationSummary classification={child} />
          ))}
        </div>
      ) : (
        <></>
      )}
      {referencedSources.length > 0 ? (
        <div className="button-and-cards-container">
          <button className="select-button" onClick={toggleSelectMode}>
            {selectMode ? "Exit Select Mode" : "Enter Select Mode"}
          </button>
          <div className="card-row-container">
            {referencedSources.map((reference: any) => (
              <div
                className={
                  selectMode &&
                  selectedProducts.some(
                    (product) => product.id === reference.id
                  )
                    ? "selected"
                    : ""
                }
                onClick={(e) => {
                  if (selectMode) {
                    e.stopPropagation();
                    e.preventDefault();
                    toggleSelectedProduct(reference as Product);
                  }
                }}
              >
                <div className={selectMode ? "no-redirect" : ""}>
                  <NodeSummary node={reference} />
                </div>
              </div>
            ))}
          </div>
        </div>
      ) : (
        <></>
      )}
    </main>
  );

};
