import React, { useContext } from "react";
import { AppContext, AppContextI } from "../../ContextWrapper";
import { useMutation, useQuery } from "@apollo/client";
import { GET_PRODUCT } from "../../gql/productQueries";
import { ClickableImage } from "../assets/ClickableImage";
import { Collapsible } from "../blocks/Collapsible";
import { EditField } from "../EditField";
import { resolvePrimaryImage } from "../../utils/utils";
import {
  SET_PRODUCT_ATTRIBUTE_VALUE,
  SET_PRODUCT_NAME,
} from "../../gql/productMutations";
import { EditAttributeField } from "../EditAttributeFied";
import { DisplayAttribute } from "../DisplayAttribute";
import "../../style/product.css";
import { DataCollapsible } from "../blocks/DataCollapsible";

export const ProductDetails = () => {
  const appContext = useContext<AppContextI>(AppContext);

  const { data, refetch } = useQuery(
    GET_PRODUCT,
    appContext.getRequestVariables()
  );

  const [setName] = useMutation(SET_PRODUCT_NAME);

  const doUpdateName = (newValue: string) => {
    setName(appContext.getRequestVariables({ newName: newValue })).then(
      refetch
    );
  };

  const [setAttribute] = useMutation(SET_PRODUCT_ATTRIBUTE_VALUE);

  const doUpdateAttribute = (newValue: string) => {
    setAttribute(appContext.getRequestVariables({ newValue: newValue })).then(
      refetch
    );
  };

  if (data) {
    const primaryImage = resolvePrimaryImage(data.product);
    const product = { ...data.product, primaryImage: primaryImage };
    return (
      <main className="product-main">
        <div className="product-container-one">
          <EditField value={product.name} onUpdate={doUpdateName} tag="h2" />
        </div>
        <section className="product-container-two">
          <div className="product-img">
            <div className="img">
              <ClickableImage
                linkedNode={product}
                asset={product.primaryImage}
              />
            </div>
            <div className="product-img-small">
              <div>
                <ClickableImage
                  linkedNode={product}
                  asset={product.primaryImage}
                />
              </div>
              <div>
                <ClickableImage
                  linkedNode={product}
                  asset={product.primaryImage}
                />
              </div>
              <div>
                <ClickableImage
                  linkedNode={product}
                  asset={product.primaryImage}
                />
              </div>
            </div>
          </div>

          <div className="product-container">
            <div className="product-text-section">
              <EditAttributeField
                node={product}
                attributeId={"50080"}
                onUpdate={doUpdateAttribute}
                tag="h3"
              />
            </div>

            <div className="product-text-section">
              <DisplayAttribute
                node={product}
                attributeId={"UIDescriptionShort"}
              />
            </div>
            <div className="collapsibles">
              <DataCollapsible />

              <Collapsible id="collapsible1" title="Instructions">
                <DisplayAttribute node={product} attributeId="50080" />
              </Collapsible>
              <Collapsible id="collapsible2" title="Preview URL">
                <DisplayAttribute node={product} attributeId="WebPreviewURL" />
              </Collapsible>
            
              <Collapsible id="collapsible4" title="ProductName">
                <DisplayAttribute node={product} attributeId="ProductName" />
              </Collapsible>
            </div>
          </div>
        </section>
        {/*     <div>
                    <div>ID: {product.id}</div>
                    <div>Name: {product.name}</div>
                </div>
                <AttributeTable product={product}/>
                <AssetTable product={product}/> */}
      </main>
    );
  } else {
    return <div>Product not found</div>;
  }
};
