import React, { useContext } from "react";
import { AppContext, AppContextI } from "../../ContextWrapper";
import { ClassificationHierarchy } from "./ClassificationHierarchy";
import { ProductDetails } from "../details/ProductDetails";

export const NodeHierarchy = () => {
  const appContext = useContext<AppContextI>(AppContext);
  return appContext.isClassificationSelection() ? (
    <ClassificationHierarchy />
  ) : appContext.isProductSelection() ? (
    <ProductDetails />
  ) : (
    <>Selection is neither a product nor a classification</>
  );
};
