import {gql} from "@apollo/client";


export const FRAGMENT_PRIMARY_IMAGE_PRODUCT = gql`
    fragment PrimaryProductImages on Product {
        primaryImages: referencesByReferenceType(referenceTypes: ["Primary Image"]) {
            referenceEntries {
                target {
                ... on Asset {
                        id
                        name
                        hasContent
                        contentUri
                    }
                }
            }
        }
}`;

export const GET_PRODUCT = gql`
  ${FRAGMENT_PRIMARY_IMAGE_PRODUCT}
  query GetProduct($context: String!,  $workspace: String!, $id: String!) {
    product(context: $context, workspace: $workspace, id: $id) { 
    id
    name
    values {
      attribute {
        id
      }
      simpleValue
    }
    ...PrimaryProductImages
  }
}
`;

export const GET_ATTRUBUTE_GROUP_VALUE = gql`
query GetProduct($context: String!, $workspace: String!, $id: String!) {
  product(context: $context, workspace: $workspace, id: $id) {
    id
    name
    valuesByDataTypeGroup(dataTypeGroups: ["UIInstruction", "UIProductDetails"]) {
      dataTypeGroup {
        id
      }
      values {
        attribute {
          id
          name
        }
        simpleValue
      }
    }
  }
}

`;
