import React from 'react';
import { useQuery } from '@apollo/client';
import { GET_ATTRUBUTE_GROUP_VALUE } from '../../gql/productQueries'; 
import { Collapsible } from '../blocks/Collapsible';

export const DataCollapsible: React.FC = () => {
  const { loading, error, data } = useQuery(GET_ATTRUBUTE_GROUP_VALUE, {
    variables: {
      context: 'English All',
      workspace: 'Main',
      id: 'A45929', 
    },
  });

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error: {error.message}</p>;

  const dataTypeGroupValues = data?.product?.valuesByDataTypeGroup[0]?.values || [];

  return (
    <Collapsible id="dataCollapsible" title="Instruction Data">
      <div>
        {dataTypeGroupValues.map((value: any) => ( 
          <div key={value.attribute.id}>
            <p>Attribute ID: {value.attribute.id}</p>
            <p>Attribute Name: {value.attribute.name}</p>
            <p>Value: {value.simpleValue}</p>
          </div>
        ))}
      </div>
    </Collapsible>
  );
};
