import React, {useContext} from "react";
import Asset from "../../assets/icons/icon.png";
import {CardColumn} from "../blocks/CardColumn";
import {Link} from "react-router-dom";
import {Classification} from "../../types/nodeTypes";
import {AppContext, AppContextI} from "../../ContextWrapper";

type Props = {
  classification: Classification;
};

export const WebChannelSummary = ({ classification }: Props) => {
  const appContext = useContext<AppContextI>(AppContext);

  const topContent = (
      <Link to={"/" + classification.id} />
  );

  return (
    <CardColumn
      topContent={topContent}
      bottomIcon={Asset}
      bottomTitle={classification.title}
      bottomOnClick={() => {
        appContext.setChannel(classification)
      }}
    />
  );
};
