import { gql } from "@apollo/client";
import { FRAGMENT_PRIMARY_IMAGE_PRODUCT } from "./productQueries";

export const FRAGMENT_PRIMARY_IMAGE_CLASSIFICATION = gql`
  fragment PrimaryClassificationImages on Classification {
    primaryImages: referencesByReferenceType(
      referenceTypes: ["Primary Image"]
    ) {
      referenceEntries {
        target {
          ... on Asset {
            id
            contentUri
          }
        }
      }
    }
  }
`;

export const GET_CLASSIFICATION_RELATIONS = gql`
  ${FRAGMENT_PRIMARY_IMAGE_CLASSIFICATION}
  ${FRAGMENT_PRIMARY_IMAGE_PRODUCT}
  query GetClassificationRelations(
    $context: String!
    $workspace: String!
    $id: String!
  ) {
    classification(context: $context, workspace: $workspace, id: $id) {
      id
      name
      title
      ...PrimaryClassificationImages
      children {
        pageElements {
          id
          title
          name
          ...PrimaryClassificationImages
          assets {
            pageElements {
              id
              contentUri
            }
          }
        }
      }
      incomingReferencesByReferenceType {
        referenceEntries {
          pageElements {
            source {
              ... on Product {
                objectType {
                  id
                }
                id
                name
                title
                ...PrimaryProductImages
              }
            }
          }
        }
      }
    }
  }
`;

export const GET_WEB_CLASSIFICATIONS = gql`
  query GetClassificationChildren($context: String!, $workspace: String!) {
    searchForClassifications(
      context: $context
      workspace: $workspace
      condition: { objectType: { objectType: "WebMainCategory" } }
    ) {
      pageElements {
        id
        name
        title
        children {
          pageElements {
            id
          }
        }
      }
    }
  }
`;

export const GET_WEB_CHANNELS = gql`
  query GetClassificationChildren($context: String!, $workspace: String!) {
    searchForClassifications(
      context: $context
      workspace: $workspace
      condition: { objectType: { objectType: "WebHierarchyRoot" } }
    ) {
      pageElements {
        id
        name
        title
      }
    }
  }
`;

export const GET_DATA_TYPE_GROUP = gql`
  query GetDataTypeGroup($context: String!, $workspace: String!, $id: String!) {
    dataTypeGroup(context: $context, workspace: $workspace, id: $id) {
      id
      attributes {
        pageElements {
          id
          name
        }
      }
      children {
        pageElements {
          id
          name
        }
      }
    }
  }
`;

