import {useQuery} from "@apollo/client";
import {GET_WEB_CHANNELS} from "../../gql/classificationQueries";
import {useContext} from "react";
import {AppContext, AppContextI} from "../../ContextWrapper";
import {WebChannelSummary} from "../details/WebChannelSummary";

export const WebChannels = () => {
    const appContext = useContext<AppContextI>(AppContext);

    const {loading, data} = useQuery(GET_WEB_CHANNELS, appContext.getRequestVariables());

    if (loading) {
        return <div>Loading...</div>
    } else if (data) {
        const pageElements: any[] = data.searchForClassifications.pageElements;
        if (pageElements && pageElements.length > 1) {
            return (
                <main className="category-main">
                    <div className="container category-container"> {
                        pageElements.map((channel: any) => {
                            return <WebChannelSummary key={channel.id} classification={channel}/>
                        })
                    } </div>
                </main>
            );
        } else {
            return <div>No web channels found</div>
        }
    } else {
        return <div>No web channels found</div>
    }
}