import {gql} from "@apollo/client";

export const SET_PRODUCT_NAME = gql(`
mutation setName($context: String!, $workspace: String!, $id: String!, $newName: String!) {
  setNodeName(context: $context, workspace: $workspace, input: {id: $id, type: Product, name: $newName}) {
    success
    errors {
      message
    }
  }
}
`);

export const SET_PRODUCT_ATTRIBUTE_VALUE = gql(`
mutation setName($context: String!, $workspace: String!, $id: String!, $newValue: String!) {
  setNodeValue(context: $context, workspace: $workspace, input: {id: $id, type: Product, attribute: "50080", value: {simpleValue: $newValue}}) {
    success
    errors {
      message
    }
  }
}
`);