import React, { useContext } from "react";
import { AppContext, AppContextI } from "../../ContextWrapper";
import { CardRow } from "../blocks/CardRow";
import { Product } from "../../types/nodeTypes";

type Props = {
  product: Product;
};

export const ProductSummary = ({ product }: Props) => {
  const appContext = useContext<AppContextI>(AppContext);

  const handleClick = () => {
    appContext.setSelection(product);
  };

  return (
    <CardRow
      product={product}
      onClick={handleClick}
      primaryImage={product.primaryImage}
    />
  );
};
